          var d = (this.shipFrom * this.selected_org.index) - (this.shipTo - this.selected_dst.index);
          // 航线最低运费变量（单位：M）
          var minReward = 5;
          // 航线方向
          var upOrDown = 'down';
          // 保证金超标
          var collateralOver = false;
          // 最大保证金变量（单位：B）
          var maxCollateral = 0;
          // 最长时效（单位：H）
          var maxHours = 24;
          console.log(d);
          tmp_m3 = this.m3.replace(/,/g,'');

          // 判断是普通还是加急
          if (this.contractType == 'accelerated') {
            // 计算挡位
            if (tmp_m3 <= 150000) {
              tmp_m3 = 150000;
            } else if (tmp_m3 <= 250000) {
              tmp_m3 = 250000;
            } else {
              tmp_m3 = 340000;
            }
            this.completeDays = '1 Day';
            this.description = '加急(accelerated)';
            this.contract_type_cn = '加急';
            this.charges = '合同（' + (tmp_m3.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,') + ' m3档位），运费标准：';
          } else {
            this.completeDays = '3 Days';
            this.description = '选填';
            this.contract_type_cn = '标准';
            this.charges = '合同，运费标准：'
          }

          // 计算超5B额外运费标识
          this.cal_extend_fee = false;
          // 慢速运输标识 true:慢7D，false:快3D
          // 20210903公告中没有说快慢了，统一按7天
          this.isRemoteLocation = true;
          // 运费不足保证金1%的标识
          this.show_collateral_info = false;

          switch (d) {
            // JITA -> 5ZXX-K
            case 1008:
              this.volumeReward = 600 * tmp_m3;
              minReward = 5;
              upOrDown = 'down';
              maxCollateral = 50;
              maxHours = 24;
              this.charges_uprice = '600 ISK/m³';
              if (tmp_collateral > 50000000000) {
                collateralOver = true;
              } else {
                this.reward = this.volumeReward;
              }
              break;
            // 5ZXX-K -> JITA
            case 1986:
              this.volumeReward = 300 * tmp_m3;
              minReward = 10;
              upOrDown = 'up';
              maxHours = 36;
              collateralReward = tmp_collateral * 0.01;
              if (tmp_collateral > 5000000000) {
                this.charges_uprice = '300 ISK/m³ + 保证金1%（即体积运费：' + (this.volumeReward.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,') + 'ISK，额外运费：' + (collateralReward.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,') + 'ISK）';
                this.reward = this.volumeReward + collateralReward;
              } else {
                this.charges_uprice = '300 ISK/m³（即体积运费：' + (this.volumeReward.toFixed() + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g,'$1,') + 'ISK）';
                this.reward = this.volumeReward;
              }
              break;
            // Egbinger -> RF-X7V
            // case 7587:
            //   this.volumeReward = 1200 * tmp_m3;
            //   this.charges_uprice = '1200 ISK/m³';
            //   collateralReward =  tmp_collateral * 0.01;
            //   if (this.volumeReward < (tmp_collateral * 0.01)) {
            //     this.reward = collateralReward;
            //     this.show_collateral_info = true;
            //   } else {
            //     this.reward = this.volumeReward;
            //   }
            //   break;
            // Podion -> RF-X7V
            // case 8480:
            //   this.volumeReward = 1400 * tmp_m3;
